.profile {
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 30px;
    padding-bottom: 120px;
}

.profile_title {
    text-align: left;
    font-weight: 600;
    background-image: var(--main-grad);
    color: transparent;
    display: inline-block;
    width: max-content;
    background-clip: text;
    width: fit-content;
}

.profile_names-block {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.profile_names-block>div {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    font-size: 24px;
}

.profile_contacts {
    display: flex;
    flex-direction: column;
    gap: 50px;
    font-size: 24px;
}

.profile_mail,.profile_phone,.profile_wallet  {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    gap: 26px;
}

.profile_button {
    width: 100%;
    border-radius: 20px;
    border: 1px solid var(--text-grey);
    background-color: transparent;
    padding: 10px 0px;
    text-align: center;
    font-size: 20px;
}